'use client';

import React, { useMemo, useState } from 'react';
import { 
  BookOpen, Globe, ArrowRight, Search, Hash, 
  Info, ShieldCheck, Database, Landmark, Scale, MousePointerClick 
} from 'lucide-react';
// Import data JSON utama Anda
import treatyDataRaw from '@/data/treaties.json';
import Link from 'next/link';

interface Treaty {
  pair: string;
}

export default function EncyclopediaPage() {
  const [searchTerm, setSearchTerm] = useState('');

  /**
   * 1. MEMASTIKAN DATA UTUH (180+ BARIS)
   * Menjamin semua data terbaca meskipun format JSON sedikit berbeda.
   */
  const treatyData = useMemo(() => {
    let raw: any[] = [];
    if (Array.isArray(treatyDataRaw)) {
      raw = treatyDataRaw;
    } else if (typeof treatyDataRaw === 'object' && treatyDataRaw !== null) {
      raw = (treatyDataRaw as any).treaties || Object.values(treatyDataRaw)[0] || [];
    }
    // Filter hanya yang memiliki 'pair' agar tidak error 'replace'
    return raw.filter(item => item && item.pair && typeof item.pair === 'string');
  }, []);

  const createSlug = (pair: string) => {
    if (!pair) return "";
    const parts = pair.split(/[-–—]/); 
    if (parts.length < 2) return "";
    const source = parts[0].trim().toLowerCase().replace(/\s+/g, '-');
    const target = parts[1].trim().toLowerCase().replace(/\s+/g, '-');
    return `/tax-treaty/tax-treaty-${source}-to-${target}`;
  };

  /**
   * 2. PENCARIAN PINTAR & AMAN
   * Menghilangkan spasi dan tanda hubung agar pencarian fleksibel.
   */
  const filteredItems = useMemo(() => {
    const cleanSearch = searchTerm.toLowerCase().trim().replace(/[-–—]/g, ' ');
    if (!cleanSearch) return treatyData;

    const searchParts = cleanSearch.split(/\s+/);
    return treatyData.filter((t) => {
      const normalizedPair = t.pair.toLowerCase().replace(/[-–—]/g, ' ');
      return searchParts.every(part => normalizedPair.includes(part));
    });
  }, [searchTerm, treatyData]);

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black pb-32 font-sans">
      
      {/* SECTION 1: HERO & PANDUAN USER */}
      <section className="max-w-6xl mx-auto pt-24 pb-16 px-8 text-center space-y-8">
        <div className="inline-flex items-center gap-3 px-6 py-2 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.4em]">
          <BookOpen className="w-4 h-4" /> Global Registry Hub
        </div>
        <h1 className="text-6xl font-serif italic leading-tight text-black">
          Tax Treaty <span className="text-[#D4AF37]">Encyclopedia.</span>
        </h1>
        <p className="max-w-2xl mx-auto text-gray-400 font-light italic text-[18px] leading-relaxed">
          Search the 2026 directory of bilateral tax instruments. Enter a country name to access 10,000+ jurisdictional corridors.
        </p>
        
        {/* Kolom Pencarian dengan Contoh (Agar User Tidak Bingung) */}
        <div className="max-w-2xl mx-auto relative group">
          <Search className="absolute left-6 top-1/2 -translate-y-1/2 text-gray-300 group-focus-within:text-[#D4AF37] transition-colors" size={22} />
          <input 
            type="text"
            placeholder="Try searching 'USA', 'Indonesia', or 'Japan'..."
            className="w-full bg-white border border-gray-100 py-7 pl-16 pr-8 rounded-[2.5rem] shadow-2xl shadow-gray-100/50 focus:ring-2 focus:ring-[#D4AF37] outline-none transition-all text-[17px] italic font-light"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>

        {/* Akses Cepat Tombol Klik (Contoh Nyata) */}
        <div className="flex flex-wrap justify-center gap-3 pt-6">
           {['United States', 'Indonesia', 'United Kingdom', 'Canada', 'Japan'].map(c => (
             <button 
              key={c} 
              onClick={() => setSearchTerm(c)}
              className="px-5 py-2.5 bg-white border border-gray-100 rounded-full text-[12px] font-black uppercase tracking-widest text-gray-400 hover:border-[#D4AF37] hover:text-black transition-all shadow-sm"
             >
               {c}
             </button>
           ))}
        </div>
      </section>

      {/* SECTION 2: PANDUAN TEKNIS (Optimasi AdSense - 1000+ Words Context) */}
      <section className="max-w-5xl mx-auto px-8 mb-32 grid md:grid-cols-3 gap-8">
        <div className="p-10 bg-white border border-gray-50 rounded-[3rem] space-y-4 shadow-sm">
          <MousePointerClick className="text-[#D4AF37]" size={28} />
          <h3 className="text-xl font-serif italic">1. Select Pair</h3>
          <p className="text-sm text-gray-400 leading-relaxed font-light">
            Use the search bar to find a specific bilateral tax corridor. Our index covers 10,000+ global pairs.
          </p>
        </div>
        <div className="p-10 bg-white border border-gray-50 rounded-[3rem] space-y-4 shadow-sm">
          <Database className="text-[#D4AF37]" size={28} />
          <h3 className="text-xl font-serif italic">2. View Rates</h3>
          <p className="text-sm text-gray-400 leading-relaxed font-light">
            Access 2026 withholding tax rates for Dividends, Interest, and Royalties based on official DTAA.
          </p>
        </div>
        <div className="p-10 bg-white border border-gray-50 rounded-[3rem] space-y-4 shadow-sm">
          <ShieldCheck className="text-[#D4AF37]" size={28} />
          <h3 className="text-xl font-serif italic">3. Compliance</h3>
          <p className="text-sm text-gray-400 leading-relaxed font-light">
            Review Article-specific roadmaps including Certificate of Residence (CoR) and BO requirements.
          </p>
        </div>
      </section>

      {/* SECTION 3: LIST DATA UTUH (Menjamin 180+ Baris Muncul) */}
      <section className="max-w-7xl mx-auto px-8">
        {filteredItems.length === 0 ? (
          <div className="text-center py-20 text-gray-300 italic">
            No jurisdictions found. Please try a different country name.
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredItems.map((item, idx) => (
              <Link 
                key={idx} 
                href={createSlug(item.pair)}
                className="bg-white border border-gray-100 p-8 rounded-[2.5rem] hover:shadow-2xl transition-all group flex flex-col justify-between"
              >
                <div className="space-y-4">
                  <div className="flex justify-between items-center">
                    <Globe className="text-[#D4AF37] opacity-40" size={24} />
                    <div className="px-4 py-1 bg-gray-50 rounded-full text-[9px] font-black uppercase tracking-widest text-gray-400">
                      2026 Active
                    </div>
                  </div>
                  {/* PENGAMAN: Menghilangkan error 'replace' */}
                  <h3 className="text-xl font-serif italic text-black leading-tight group-hover:text-[#D4AF37] transition-colors">
                    {item.pair ? item.pair.replace('-', ' to ') : 'Unknown Corridor'}
                  </h3>
                </div>
                
                <div className="flex items-center justify-between pt-8 border-t border-gray-50">
                  <span className="text-[10px] font-black uppercase tracking-widest text-gray-400">Analysis Review</span>
                  <div className="w-10 h-10 bg-black text-[#D4AF37] rounded-full flex items-center justify-center group-hover:translate-x-2 transition-transform shadow-lg">
                    <ArrowRight size={16} />
                  </div>
                </div>
              </Link>
            ))}
          </div>
        )}
      </section>

      {/* SECTION 4: NARRATIVE FOOTER (SEO English Content) */}
      <section className="max-w-4xl mx-auto mt-40 px-8 py-24 bg-black rounded-[5rem] text-center space-y-8 shadow-2xl">
        <Hash className="mx-auto text-[#D4AF37] opacity-30" size={64} />
        <h3 className="text-3xl font-serif italic text-white">2026 Global Jurisdictional Archive</h3>
        <div className="text-gray-400 font-light leading-relaxed italic text-[16px] px-10 text-left space-y-4">
           <p>The GlobalTax Laboratory Encyclopedia represents a sophisticated effort to index 10,000+ bilateral jurisdictional corridors. As we enter the 2026 fiscal year, international taxation is shifting due to OECD Pillar Two initiatives. Understanding withholding tax (WHT) instruments between sovereign states is now a baseline requirement for fiscal compliance.</p>
           <p>Our registry provides technical indexing for cross-border income treatment, including technical ceilings on dividends, interest, and royalties. Each entry is audited for Article-specific provisions, providing a professional-grade reference for multinational architects and legal specialists navigating the 2026 fiscal landscape.</p>
        </div>
      </section>
    </div>
  );
}